.form {
  display: flex;
  flex-direction: column;
}

.form label {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 30px;

    @include vp-767 {
      margin-bottom: 26px;
    }
  }
}

.form input {
  padding: 11px 16px;
  border: none;
  width: 100%;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  background-color: $color-alice-blue;

  &::placeholder {
    color: rgba(1, 28, 64, 0.7);
  }

  @include hover-focus {
    outline: 2px solid $color-neon-blue;
    outline-offset: -4px;
  }
}

.form input[type="checkbox"] {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.form input[type="checkbox"] + span {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  min-width: 18px;
  min-height: 18px;
  background-color: $color-alice-blue;

  @include vp-767 {
    min-width: 20px;
    min-height: 20px;
  }
}

.form input[type="checkbox"]:checked + span::after {
  position: absolute;
  content: "";
  top: 5px;
  left: 3px;
  background-image: url("../img/svg/checkbox-arrow.svg");
  width: 12px;
  height: 8px;
}

.form input[type="checkbox"]:focus + span {
  outline: 1px solid $color-neon-blue;
  outline-offset: -2px;
}

.form input[type="checkbox"]:hover + span {
  outline: 1px solid $color-neon-blue;
  outline-offset: -2px;
}

.form label span + span {
  display: inline-block;
  vertical-align: middle;
  padding-left: 5px;
  font-size: 10px;
  line-height: 13px;

  @include vp-767 {
    padding-left: 8px;
    line-height: 12px;
  }
}

.form label[data-tel-label] {
  position: relative;
}

.form label[data-tel-label] span {
  position: absolute;
  top: 44px;
  font-size: 12px;
  line-height: 16px;
}

.form button {
  background-color: $color-alice-blue;
  padding: 12px 10px 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: $color-tangaroa;
  text-align: center;
  border: none;
  width: 100%;
  cursor: pointer;

  @include hover {
    opacity: 0.8;
  }

  @include focus {
    outline: 2px solid $color-neon-blue;
    outline-offset: -4px;
  }

  &:disabled {
    opacity: 0.6;
  }
}
